import style from './LoginPage.module.scss'
import { ReactComponent as Logo } from '../../assets/icons/logo.svg'
import { Link, useNavigate } from 'react-router-dom'
import { AuthInput } from '../../components'
import { useAuth } from '../../context/AuthContext';
import { useState } from 'react'
import Swal from 'sweetalert2'
import { useEffect } from 'react';

const LoginPage = () => {
  const [account, setAccount] = useState('')
  const [password, setPassword] = useState('')

  const { login, isAuthenticated, user, responseError, errorInfo } = useAuth();

  const navigate = useNavigate()
  const authInputCollection = [
    { label: '', id: '', type: 'text', placeholder: '', value: account, onChange: (accountValue) => setAccount(accountValue) },
    { label: '', id: '', type: 'password', placeholder: '', value: password, onChange: (passwordValue) => setPassword(passwordValue) }]



  const handleLoginSubmit = async (e) => {
    e.preventDefault();
    if (!account.trim() || !password.trim()) {
      Swal.fire({
        title: '',
        icon: 'error',
        showConfirmButton: false,
        timer: 2000,
        position: 'top',
      });
      return
    }

    const success = await login({ account, password });

    if (success) {
      Swal.fire({
        title: '',
        icon: 'success',
        showConfirmButton: false,
        timer: 2000,
        position: 'top',
      });
      // setResponseError(false)
      console.log(responseError)
      navigate('/')
      return
    }

    if (!success) {
      console.log(responseError)
      console.log(errorInfo)//
      Swal.fire({
        title: '',
        icon: 'error',
        showConfirmButton: false,
        timer: 2000,
        position: 'top',
      });
    }



    // Swal.fire({
    //   title: '',
    //   icon: 'error',
    //   showConfirmButton: false,
    //   timer: 1000,
    //   position: 'top',
    // });
    // setResponseError(true)
    // return
  }
  useEffect(() => {
    if (isAuthenticated) {
      navigate(`/${user?.id}/`);
    }
  }, [navigate, isAuthenticated, user]);


  return (
    <div className={style.container}>
      <Logo className={style.logo} />
      <h3 className={style.title}>Alphitter</h3>
      <form className={style.form} onSubmit={handleLoginSubmit}>
        {authInputCollection.map(({ label, id, type, placeholder, value, maxLength, onChange }) => (
          <AuthInput
            key={id}
            label={label}
            id={id}
            type={type}
            placeholder={placeholder}
            value={value}
            maxLength={maxLength}
            onChange={onChange}
            responseError={responseError}
            errorInfo={errorInfo}
          />
        ))}
        <button className={style.button} type="submit" ></button>
      </form >
      <div className={style.linkGroup}>
        <Link to='/register' className={style.link}></Link>
        
        <Link to='/admin' className={style.link}></Link>
      </div>
    </div>
  )
}
export default LoginPage