import style from './Headers.module.scss';
//import { useAuthContext } from ''; // 
//import { Link } from 'react-router-dom';
import LeftIcon from '../../assets/icon/back.svg';
//: & username & &'
//TODO: 

const Header = ({ currentSection, setCurrentSection, otherUser, otherUserTweetCount }) => {
  //const {userData, userTweetCount} = useAuthContext();

  const handleClickSection = () => {
    setCurrentSection('mainPage');
  }

  let headerContext = "";

  switch (currentSection) {
    case 'homePage':
      headerContext = '';
      break;
    case 'replyPage':
      headerContext =
        <>
          <img src={LeftIcon} alt="left" onClick={handleClickSection} />
          
        </>;
      break;
    case 'otherUserPage':
      headerContext =
        <>
          <img src={LeftIcon} alt="left" onClick={handleClickSection} />
          <span>{otherUser}</span>
          <div>{otherUserTweetCount} </div>
        </>;
      break;
    case 'settingPage':
      headerContext = '';
      break;
    default:
      headerContext = '';
  }

  return (
    <div className={style.HeaderContainer}>
      {headerContext}
    </div>
  )
}

export default Header;



